/* --- mainNav --------------------- */
.mainNav {
	padding-right: 15px;
	float: right;
  }
  .mainNav .navTabs {
	float: right;
  }
  .mainNav .navTabs > li {
	float: left;
	margin-left: 10px;
	position: relative;
  }
  .mainNav .navTabs > li > a,
  .mainNav .navTabs > li > button {
	display: block;
	height: 33px;
	line-height: 33px;
	text-align: center;
	padding: 0px 12px;
	color: #21242e;
	font-size: 14px;
	font-weight: 700;
	-webkit-border-radius: 2px 2px 2px 2px;
	-moz-border-radius: 2px 2px 2px 2px;
	-o-border-radius: 2px 2px 2px 2px;
	-ms-border-radius: 2px 2px 2px 2px;
	-khtml-border-radius: 2px 2px 2px 2px;
	border-radius: 2px 2px 2px 2px;
  }
  .mainNav .navTabs > li .active {
	background-color: #ff6b6b;
	color: #ffffff !important;
  }
  /* --- responsiveMainNav --------------------- */
  #responsiveMainNavToggler {
	padding: 0;
	height: 33px;
	width: 33px;
	text-align: center;
	line-height: 33px;
	margin-top: 30px;
	margin-bottom: 30px;
	font-size: 18px;
	overflow: hidden;
	margin-right: 15px;
	float: right;
	display: none;;
  }
  #responsiveMainNavToggler.opened {
	background-color: #ff6b6b;
	color: #ffffff;
  }
  .style-5 #responsiveMainNavToggler {
	background-color: #ff6b6b;
	color: #ffffff;
  }
  .style-5 #responsiveMainNavToggler:hover {
	background-color: #21242e;
  }
  .responsiveMainNav {
	padding: 0 15px;
	margin-bottom: 30px;
	position: relative;
	display: none;
  }
  .responsiveMainNav > ul.navTabs > li a,
  .responsiveMainNav > ul.navTabs > li > button {
	display: block;
	width: 100%;
	height: 40px;
	line-height: 40px;
	padding: 0 15px;
	background-color: #f5f6f8;
	color: #21242e;
	border-bottom: 1px solid #cccccc;
	text-align: left;
	font-weight: 700;
  }
  .responsiveMainNav > ul.navTabs > li a:hover,
  .responsiveMainNav > ul.navTabs > li > button:hover {
	background-color: #ff6b6b;
	color: #ffffff;
  }
  .responsiveMainNav > ul.navTabs > li a:hover:after,
  .responsiveMainNav > ul.navTabs > li > button:hover:after {
	color: #ffffff !important;
  }
  .responsiveMainNav > ul.navTabs > li a.toggleResponsive:after,
  .responsiveMainNav > ul.navTabs > li > button.toggleResponsive:after {
	content: '+';
	font-family: 'tahoma';
	float: right;
	color: #21242e;
	font-size: 17px;
	font-weight: 700;
	-webkit-transition: all 0.4s ease-in-out;
	-moz-transition: all 0.4s ease-in-out;
	-o-transition: all 0.4s ease-in-out;
	-ms-transition: all 0.4s ease-in-out;
	-khtml-transition: all 0.4s ease-in-out;
	transition: all 0.4s ease-in-out;
  }
  .responsiveMainNav > ul.navTabs > li .toggleResponsive.activeLine {
	background-color: #ff6b6b;
	color: #ffffff;
  }
  .responsiveMainNav > ul.navTabs > li .toggleResponsive.activeLine:after {
	content: '-';
	font-family: 'tahoma';
	float: right;
	color: #ffffff;
	font-size: 17px;
	font-weight: 700;
  }
  .responsiveMainNav > ul.navTabs > li .active {
	background-color: #ff6b6b;
	color: #ffffff;
  }
  .responsiveMainNav > ul.navTabs > li .active:after {
	color: #ffffff !important;
  }
